const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState = {
    newMessageText: "new message)",
    messages: [
        {id: 1, name: "Dima"},
        {id: 2, name: "Sveta"},
        {id: 3, name: "Oleg"},
        {id: 4, name: "Igor"},
        {id: 5, name: "Egor"},
        {id: 6, name: "Vasya"},
        {id: 7, name: "Petya"},
    ],
    posts: [
        {id: 1, message: "hi"},
        {id: 2, message: "yo yo yo"},
        {id: 3, message: "how r u"},
        {id: 4, message: "whoohoo"},
        {id: 5, message: "dddddd"},
        {id: 6, message: "stay where u r"},
        {id: 7, message: "give me something"},
    ],
};

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {

        case SEND_MESSAGE: {
            let body = action.newMessageBody;
            return {
                ...state,
                posts: [...state.posts, {id: 6, message: body}]};
        }

        default:
            return state;
    }
}
export const sendMessageCreator = (newMessageBody) => ({type: SEND_MESSAGE, newMessageBody})
export default dialogsReducer;