import React from 'react';
import Paginator from "../Common/Paginator/Paginator";
import User from "./User";


let Users = ({currentPage, onPageChanged, pageSize, totalUsersCount, users, ...props}) => {
    return <div>
        <Paginator totalItemsCount={totalUsersCount} pageSize={pageSize} onPageChanged={onPageChanged}
                   currentPage={currentPage}/>

        <div>
            {
                users.map(u => <User followingInProgress={props.followingInProgress}
                                     user={u}
                                     unfollow={props.unfollow}
                                     follow={props.follow}
                                     key={u.id}/>)
            }
        </div>
    </div>
}


export default Users;