import {profileAPI, usersAPI} from "../api/api";

const ADD_POST = 'ADD-POST';
//const ADD_NEW_POST_TEXT = 'ADD-NEW-POST-TEXT';
const SET_USERS_PROFILE = 'SET_USERS_PROFILE';
const SET_STATUS = 'SET_STATUS';
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';


let initialState = {
    data: [
        {id: 1, likesCount: 15, message: "hi"},
        {id: 2, likesCount: 16, message: "yo yo yo"},
        {id: 3, likesCount: 17, message: "how r u"},
        {id: 4, likesCount: 18, message: "whoohoo"},
        {id: 5, likesCount: 22, message: "dddddd"},
        {id: 6, likesCount: 155, message: "stay where u r"},
        {id: 7, likesCount: 155, message: "give me something"},
    ],
    profile: null,
    status: ""
};


const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            let newData = {
                id: 8,
                likesCount: 0,
                message: action.newPostText,
            };
            let stateCopy = {...state};
            stateCopy.data = [...state.data];
            stateCopy.data.push(newData);
            stateCopy.newPostText = '';
            return stateCopy;
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }

        case SET_USERS_PROFILE: {
            let stateCopy = {...state};
            stateCopy.profile = action.profile;
            return stateCopy;
        }
        case SAVE_PHOTO_SUCCESS: {
            return {...state, profile: {...state.profile, photos: action.photos }};
        }
        default:
            return state;

    }
};

export const setStatus = (status) => {
    return {type: SET_STATUS, status}
};
export const addPostActionCreator = (newPostText) => {
    return {type: ADD_POST, newPostText}
};
export const setUserProfile = (profile) => {
    return {type: SET_USERS_PROFILE, profile}
};
export const savePhotoSuccess = (photos) => {
    return {type: SAVE_PHOTO_SUCCESS, photos}
};

//thunks
export const getUserProfile = (userId) => async (dispatch) => {
    let response = await usersAPI.getProfile(userId);
    debugger;
    dispatch(setUserProfile(response.data));
};
export const getStatus = (userId) => async (dispatch) => {
    let response = await profileAPI.getStatus(userId);
    dispatch(setStatus(response.data));

};
export const updateStatus = (status) => async (dispatch) => {
    let response = await profileAPI.updateStatus(status);
    if (response.data.resultCode === 0)
        dispatch(setStatus(response.data));

};
export const savePhoto = (file) => async (dispatch) => {
    let response = await profileAPI.savePhoto(file);
    if (response.data.resultCode === 0)
        dispatch(savePhotoSuccess(response.data.photos));

};

export default profileReducer;